import { Link } from 'react-router-dom'
import styled, { css } from 'styled-components'
import { Flex, Text } from '@revolut/ui-kit'

const DEFAULT_LINK_OPACITY = 0.5

export const VerticalAligned = styled(Flex)`
  flex-direction: column;
  justify-content: center;
`

export const LinkStyles = css`
  position: relative;
  color: inherit;
  text-decoration: none;
  transition: color 0.1s ease, opacity 0.1s ease;
  font-weight: 500;
  opacity: ${(props) => (props.isActive ? 1 : DEFAULT_LINK_OPACITY)};

  &:hover {
    opacity: 1;
  }
`
export const StyledLink = styled.a`
  ${LinkStyles}
`;


export const StyledLinkText = styled(Text)`
  ${LinkStyles}
  cursor: pointer;
`

export const ExternalLink = styled.a.attrs({
  target: '_blank',
  rel: 'noreferrer noopener',
})``

export const StyledRouterLink = styled(Link)`
  ${LinkStyles}
  opacity: ${(props) => (props.active ? 1 : 0.5)};
`

export const StyledExternalLink = styled(ExternalLink)`
  ${LinkStyles}
`

export const StyledMenuIcon = styled.div`
  width: 1.75rem;
  height: 1.75rem;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 1.125rem;
    height: 0.125rem;
    left: 0.3rem;
    border-radius: 0.125rem;
    transition: background-color 0.2s ease,
      transform 0.3s cubic-bezier(0.4, 0.01, 0.165, 0.99);
    background-color: white;
  }

  &::before {
    top: 0.5rem;
  }

  &::after {
    top: 1.125rem;
  }

  ${({ isOpen }) =>
    isOpen &&
    css`
      &::before {
        transform: translateY(0.3125rem) rotate(-45deg);
      }

      &::after {
        transform: translateY(-0.3125rem) rotate(45deg);
      }
    `};
`

export const NarrowNavbarFooter = styled(Flex).attrs({
  bg: 'themeSecondary',
  p: 2,
})`
  width: 100%;
  justify-content: center;

  ul {
    border-top: none !important;
    padding-top: 0% !important;
  }
`
export const FlexColumn = styled(Flex)`
  flex-direction: column;
`

export const NarrowNavbarContent = styled(FlexColumn)`
  width: 100%;
  flex: 1;
  position: fixed;
  left: 0;
  z-index: ${(props) => (props.isOpen ? 2 : -1)};
  top: ${(props) => props.navbarHeight}px;
  transition: opacity 0.2s ease-in-out 0.05s;
  height: calc(100% - ${(props) => props.navbarHeight}px);
  opacity: ${(props) => (props.isOpen ? 1 : 0)};

  ul {
    border-top: 1px solid rgb(77, 82, 88);
    padding-top: 16px;
  }

  ul > li {
    padding: 0px;
  }

  ul > li > * {
    padding: 1rem 1.2rem;
  }
`

export const SpacedBetween = styled(Flex)`
  justify-content: space-between;
`

export const StyledNavbar = styled(SpacedBetween).attrs({ as: 'header' })``
