/* eslint-disable react/prop-types */
import React, { forwardRef } from "react";
import {
  StyledLink,
  StyledExternalLink,
  StyledLinkText,
} from "./Navbar.styles";

const getIsActivePath = (path) => window.location.pathname.startsWith(path);

export const MenuItemLink = forwardRef(({ to, ...rest }, ref) => {
  if (to) {
    return (
      <StyledLink
        {...rest}
        ref={ref}
        href={to}
        isActive={getIsActivePath(to)}
      />
    );
  }

  return <StyledExternalLink {...rest} ref={ref} />;
});

export const MenuItem = forwardRef(({ label, path, submenu, ...rest }, ref) => {
  const menuMatchesPathname = (val) => getIsActivePath(val.to);

  if (submenu) {
    return (
      <StyledLinkText
        className="menu--item"
        ref={ref}
        isActive={submenu.some(menuMatchesPathname)}
      >
        {label}
      </StyledLinkText>
    );
  }

  return (
    <MenuItemLink className="menu--item" {...rest} ref={ref}>
      {label}
    </MenuItemLink>
  );
});
