import React from "react";
import ReactDOM from "react-dom";
import { ThemeProvider } from "styled-components";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import reportWebVitals from "./reportWebVitals";
import { FaceliftTheme } from "./theme";
import App from "./App";
import "./index.css";

import merchantAPISpec from "./yaml/merchant.yaml";
import businessAPISpec from "./yaml/business.yaml";
import openBankingAPISpec from "./yaml/open-banking.yaml";

const BASE_PATH = "/api-reference";
const ROUTES = [
  {
    path: "/merchant",
    apiSpec: merchantAPISpec,
  },
  {
    path: "/business",
    apiSpec: businessAPISpec,
  },
  {
    path: "/open-banking",
    apiSpec: openBankingAPISpec,
  },
];

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={FaceliftTheme}>
      <Router basename={BASE_PATH}>
        <Switch>
          {ROUTES.map((routeConfig) => (
            <Route exact path={routeConfig.path} key={routeConfig.path}>
              <App apiSpec={routeConfig.apiSpec} />
            </Route>
          ))}
        </Switch>
      </Router>
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

// Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
