import React, { useEffect, useState } from "react";
import { Box, Flex } from "@revolut/ui-kit";
import { VerticalAligned, StyledLink } from "./Navbar.styles";
import { PageHeaderSection } from "../PageHeader/PageHeader.styles";
import { NavbarMenu, Menu } from "./Menu";
import {
  StyledMenuIcon,
  NarrowNavbarContent,
  NarrowNavbarFooter,
  StyledNavbar,
} from "./Navbar.styles";
import { Logo } from "./Logo";
import { useNavbarDimensions } from "./useNavbarDimensions";

export const Navbar = ({ items, isNavbarNarrow }) => {
  const { verticalMargin, navbarHeight } = useNavbarDimensions();
  const [menuOpen, setMenuOpen] = useState(false);
  const navbarMenu = <NavbarMenu items={items} isNarrow={isNavbarNarrow} />;

  useEffect(() => {
    if (menuOpen && isNavbarNarrow) {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      document.body.classList.add("noscroll");
    } else {
      document.body.classList.remove("noscroll");
    }
  }, [menuOpen, isNavbarNarrow]);

  const actions = (
    <Flex>
      <Menu
        items={[
          {
            href: "https://www.revolut.com/careers/department/technology",
            label: "We're hiring!",
          },
        ]}
      />
    </Flex>
  );

  return (
    <StyledNavbar
      padding={`${verticalMargin}px 0px`}
      height={`${navbarHeight}px`}
    >
      <Flex>
        <VerticalAligned mr={3}>
          <StyledLink isActive href={"/"}>
            <Logo />
          </StyledLink>
        </VerticalAligned>

        {!isNavbarNarrow && navbarMenu}
      </Flex>

      <VerticalAligned>
        {isNavbarNarrow ? (
          <Box style={{ position: "relative" }} use="button" bg="inherit">
            <StyledMenuIcon
              isOpen={menuOpen}
              onClick={() => setMenuOpen(!menuOpen)}
            />
          </Box>
        ) : (
          actions
        )}
      </VerticalAligned>

      {isNavbarNarrow && (
        <NarrowNavbarContent
          isOpen={menuOpen}
          navbarHeight={navbarHeight}
          bg="themeColor"
        >
          <PageHeaderSection flex={1}>{navbarMenu}</PageHeaderSection>
          <NarrowNavbarFooter>{actions}</NarrowNavbarFooter>
        </NarrowNavbarContent>
      )}
    </StyledNavbar>
  );
};
