import React, { memo, useEffect } from "react";

import { Navbar } from "../Navbar";
import { Box } from "@revolut/ui-kit";
import { useWindowSize } from "../useWindowSize";
import { PageHeaderSection } from "./PageHeader.styles";

const MENU_ITEMS = [
  {
    label: "Documentation",
    submenu: [
      {
        label: "Accept Payments",
        to: "/docs/accept-payments",
      },
      {
        label: "Manage Accounts",
        to: "/docs/manage-accounts",
      },
      {
        label: "Build Banking Apps",
        to: "/docs/build-banking-apps",
      },
    ],
  },
  {
    label: "API Reference",
    submenu: [
      {
        label: "Merchant API",
        to: "/api-reference/merchant",
      },
      {
        label: "RevolutCheckout.js",
        to: "/docs/revolut-checkout-js",
      },
      {
        label: "Business API",
        to: "/api-reference/business",
      },
      {
        label: "Open Banking API",
        to: "/api-reference/open-banking",
      },
    ],
  },
  /*
  {
    label: "Partner program",
    href: "https://developer.revolut.com/partner",
  },
  */
];

const PAGE_HEADER_WRAPPER_STYLES = {
  position: "fixed",
  top: 0,
  left: 0,
  right: 0,
  zIndex: 1000,
};

export const PageHeader = memo(() => {
  const { width = 0 } = useWindowSize();
  const isNavbarNarrow = width < 900;

  /**
   * Hack to preserve fragment identifiers in Slate
   * TODO: could be removed once rendered inline in Slate
   */

  useEffect(() => {
    const API_REF_REGEX = /\/api-reference\//;
    const LOCATION_HREF = window.location.href;

    if (API_REF_REGEX.test(LOCATION_HREF)) {
      return;
    }

    const { hash } = new URL(LOCATION_HREF);
    if (!hash) {
      return;
    }

    const el = document.querySelector(hash);
    if (!el) {
      return;
    }

    el.scrollIntoView();
  }, []);

  return (
    <Box
      bg="themeColor"
      color="white"
      width="100%"
      style={isNavbarNarrow ? {} : PAGE_HEADER_WRAPPER_STYLES}
    >
      <PageHeaderSection>
        <Navbar items={MENU_ITEMS} isNavbarNarrow={isNavbarNarrow} />
      </PageHeaderSection>
    </Box>
  );
});
