import { useState, useEffect } from "react";

const RAW_NAVBAR_HEIGHT = 88;
const NO_SCROLL_RAW_NAVBAR_HEIGHT = 128;
const NO_SCROLL_VERTICAL_MARGIN =
  (NO_SCROLL_RAW_NAVBAR_HEIGHT - RAW_NAVBAR_HEIGHT) / 2;

export const useNavbarDimensions = () => {
  const [verticalMargin, setVerticalMargin] = useState(
    NO_SCROLL_VERTICAL_MARGIN
  );
  const navbarHeight = RAW_NAVBAR_HEIGHT + verticalMargin * 2;

  useEffect(() => {
    const handler = () => {
      if (window.scrollY <= NO_SCROLL_VERTICAL_MARGIN) {
        setVerticalMargin(NO_SCROLL_VERTICAL_MARGIN - window.scrollY);
      } else {
        setVerticalMargin(0);
      }
    };

    window.addEventListener("scroll", handler);
    window.scrollTo(0, 0);

    return () => {
      window.removeEventListener("scroll", handler);
    };
  }, []);

  return { verticalMargin, navbarHeight };
};
