import React, { useState } from "react";
import styled from "styled-components";
import * as Icons from "@revolut/icons";
import { Box, Flex, Dropdown } from "@revolut/ui-kit";
import { MenuItem } from "./MenuItem";
import { useDropdown } from "../useDropdown";
import { VerticalAligned, FlexColumn } from "./Navbar.styles";

const SpacedBetween = styled(Flex)`
  justify-content: space-between;
`;

const StyledDropdownItem = styled(Dropdown.Item)`
  color: white;
  padding: 0;

  > * {
    padding: 1rem;
    display: block;
  }

  :hover {
    background: inherit !important;
  }
`;

export const MenuListItem = styled(VerticalAligned).attrs({ use: "li" })`
  .menu--item {
    font-size: 17px;
    letter-spacing: -0.025rem;
    font-family: "Basier Circle", Roboto, -apple-system, "Helvetica Neue",
      Helvetica, "Segoe UI", Arial, sans-serif;
  }
`;

export const NavbarMenu = ({ items, isNarrow }) => {
  return (
    <Box use="nav" flex={1}>
      <Flex height="100%" justifyContent={isNarrow ? "flex-start" : "center"}>
        <Menu
          inlineSubmenu={isNarrow}
          flexDirection={isNarrow ? "column" : "row"}
          items={items}
        />
      </Flex>
    </Box>
  );
};

const StyledDropdown = styled(Dropdown)`
  > * {
    border-radius: 8px;
  }
`;

const DropdownSubmenuMenuItem = (props) => {
  const {
    isOpen,
    anchorRef,
    anchorCallbackRef,
    submenuCallbackRef,
  } = useDropdown();

  return (
    <>
      <MenuListItem key={props.label} p="0px 1.2rem">
        <MenuItem {...props} ref={anchorCallbackRef} />
      </MenuListItem>
      {props.submenu && (
        <StyledDropdown
          ref={submenuCallbackRef}
          anchorRef={anchorRef}
          isOpen={isOpen}
          bg="rgb(42, 45, 50)"
          minWidth="256px"
          paddingTop={1}
          paddingBottom={1}
          placement="bottom-start"
          mt={1}
          zIndex={1000}
        >
          {props.submenu.map((menuItem) => {
            return (
              <StyledDropdownItem key={menuItem.label}>
                <MenuItem {...menuItem} />
              </StyledDropdownItem>
            );
          })}
        </StyledDropdown>
      )}
    </>
  );
};

const StyledInlineMenuItem = styled(SpacedBetween)`
  cursor: pointer;
  svg {
    opacity: 0.5;
  }

  :hover {
    svg,
    span {
      opacity: 1;
    }
  }
`;

const InlineSubmenuMenuItem = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <VerticalAligned key={props.label} use="li" p="0px 1.2rem">
      <StyledInlineMenuItem onClick={() => setIsOpen((prev) => !prev)}>
        <MenuItem {...props} />
        {isOpen ? (
          <Icons.ArrowThinUp size={16} />
        ) : (
          <Icons.ArrowThinDown size={16} />
        )}
      </StyledInlineMenuItem>
      <FlexColumn
        use="ul"
        maxHeight={isOpen ? "300px" : 0}
        opacity={isOpen ? 1 : 0}
        marginLeft={1}
        style={{
          transition:
            "max-height 0.2s ease-in 0s, padding 0.1s ease, opacity 0.1s ease",
          borderTop: "none",
          padding: isOpen ? undefined : 0,
        }}
      >
        {props.submenu.map((item) => {
          return (
            <MenuListItem
              key={item.label}
              display={isOpen ? undefined : "none"}
            >
              <MenuItem {...item} />
            </MenuListItem>
          );
        })}
      </FlexColumn>
    </VerticalAligned>
  );
};

const MenuItemContainer = ({ inlineSubmenu, ...props }) => {
  if (props.submenu) {
    if (inlineSubmenu) {
      return <InlineSubmenuMenuItem {...props} />;
    }
    return <DropdownSubmenuMenuItem {...props} />;
  }

  return (
    <MenuListItem key={props.label} p="0px 1.2rem">
      <MenuItem {...props} />
    </MenuListItem>
  );
};

export const Menu = ({ items, inlineSubmenu, ...flexProps }) => {
  return (
    <Flex use="ul" width="100%" {...flexProps}>
      {items.map((menuItemProps) => {
        return (
          <MenuItemContainer
            key={menuItemProps.label}
            inlineSubmenu={inlineSubmenu}
            {...menuItemProps}
          />
        );
      })}
    </Flex>
  );
};
