import { useRef, useState, useEffect } from 'react'
import { useHover } from './useHover'

export const useDropdown = ({ closeDelay = 200 } = {}) => {
  const [isOpen, setIsOpen] = useState(false)
  const closeTimeout = useRef()

  const {
    value: anchorHovered,
    callbackRef: anchorCallbackRef,
    ref: anchorRef,
  } = useHover()
  const { value: submenuHovered, callbackRef: submenuCallbackRef } = useHover()

  useEffect(() => {
    if (anchorHovered || submenuHovered) {
      setIsOpen(true)
    } else {
      closeTimeout.current = setTimeout(() => setIsOpen(false), closeDelay)
    }

    return () => {
      clearTimeout(closeTimeout.current)
    }
  }, [anchorHovered, closeDelay, submenuHovered])

  return { isOpen, anchorRef, anchorCallbackRef, submenuCallbackRef }
}
